	let routing = () => {
		let routingURL = 'https://developers.onemap.sg/privateapi/routingsvc/route?start=1.27948100992947,103.844116761408&end=1.37550739111956,103.902779987401&routeType=pt&token={token}&date=2017-07-24&time=23:00:00&mode=TRANSIT&maxWalkDistance=1000&numItineraries=2'
		
		routingURL = routingURL.replace('{token}', API_TOKEN);
		
		console.log('Routing url = ', routingURL);
		
		$.ajax({
			"url": routingURL,
			"async": true,
			"crossDomain": true,
			"contentType": false
		}).done(function(response){
			
			let	itineraries = response.plan.itineraries;
				
			for(let itinerary of itineraries) {
				let polyline = [];
				let legs = itinerary.legs;
				for(let leg of legs) {
					
					polyline.push(L.polyline(polyutil.decode(leg.legGeometry.points), {color: "red"}));
					
				}
				
				for(let i = 0; i < polyline.length; i++) {
					polyline[i].addTo(map);
				}
			}
			
		})
	};
	
	let drive = () => {
		let url = 'https://developers.onemap.sg/privateapi/routingsvc/route?start=1.27948100992947,103.844116761408&end=1.37550739111956,103.902779987401&routeType=drive&token={token}';
		
		url = url.replace('{token}', API_TOKEN);
		$.ajax({
			"url": url,
			"async": true,
			"crossDomain": true,
			"contentType": false
		}).done(function(response){
			console.log('response -- ', response);
			
			let alt = [];
			
			let polyline = L.polyline(polyutil.decode(response.route_geometry, 5), {color: "red"});
			let primary = {};
			primary.path = polyline;
			let HTMLInstructions = '<ol>';
			for(let i=0; i < response.route_instructions.length; i++) {
				HTMLInstructions += '<li>' + response.route_instructions[i][9] + '</li>';
			}
			HTMLInstructions += '</ol>';
			
			primary.instructions = HTMLInstructions;
			alt.push(primary);
			
			if(response.alternativeroute) {
				let data = response.alternativeroute[0];
				let polyline = L.polyline(polyutil.decode(data.route_geometry, 5), {color: "red"});
				let alternative = {};
				// alt["alternative"] = [];
				alternative.path = polyline;
				let HTMLInstructions = '<ol>';
				for(let i=0; i < data.route_instructions.length; i++) {
					HTMLInstructions += '<li>' + data.route_instructions[i][9] + '</li>';
				}
				HTMLInstructions += '</ol>';
				
				alternative.instructions = HTMLInstructions;
				alt.push(alternative);
			}
			
			console.log('alt - ', alt);
			
			let $routingHeader = $('.routing-header');

			$.each($routingHeader, function(index, val) {
				$(this).on('click', function(){
					console.log('index -- ', index);
					alt[index].path.addTo(map);
				});
			});

		});
			
	};
	
